    async function batchPromises(promiseGenerators, batchSize = 5) {
        let results = [];
        while (promiseGenerators.length > 0) {
            const batchResults = await Promise.all(promiseGenerators.splice(0, batchSize).map(f => f()));
            results = [...results, ...batchResults];
        }
        return results;
    }
